@import url('https://fonts.googleapis.com/css2?family=Oswald&family=Poppins:ital,wght@0,200;0,400;0,600;1,500&display=swap');
*{
  margin: 0;
  border-radius: 60px;
  text-align: center;
  box-sizing: border-box;
  line-height: 1.5;
  scroll-behavior: smooth;
  font-family:Georgia, 'Times New Roman', Times, serif;
}
.container {
  width: 80%;
  margin: auto;
  overflow: hidden;
 
}
.first-child {
  background-image:linear-gradient(rgba(4,9,30,0.5),rgba(4,9,30,0.5)),url(https://static1.colliderimages.com/wordpress/wp-content/uploads/2022/09/Fall-Anime-Feature.jpg);
  height: 70vh;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  background-size: cover;

 
  min-height: 100%;
}
 

 
  

.header {
  font-size: 3rem;
  margin-top: 50px;
}

.mini span{
  margin-top: 2rem;
  display: block;
  text-align: left;
  font-size: 3rem;
  flex: 0 0 60%;
  
}
#ko {
  text-align: center;
  border-color: red;
  border-style: ridge;
  
}

.mini p {
  font-size: 2rem;
  text-align: left;
  margin-top: 70px;
  flex-wrap: nowrap;
} 

.image {
  width: 40%;
  height: 400px;
}

.second h2{
  text-align: left;
  font-size: 3rem;
  margin: 3rem;
  color: rgb(67, 154, 180); 
  text-shadow: 1px 1px black;
  word-wrap: normal;


}

.star{
  height: 60px;
  width: 20px;
}
.first-child h3 {
  color: rgb(245, 233, 69);
  top: 50%;
  left: 50%;
  margin: auto;
  position: absolute;
  transform: translate(-50%,-50%);
  font-family: cursive;
  font-size: 50px;
  word-spacing: 20px;   
  background: transparent;
}
#adjust {
 
  margin-left: 12px;
  margin-top: 3px;
  
}
.media {
  display: flex;
  justify-content: space-around;
  display: 0 0 40%;
}
.video {
  height: 400px;
   width :40%;
   border-radius: 30px;
   max-width: 100%;
   
}
div .bottom {
  
  margin-top: 40px;
  border-radius: 0;
  font-size: 1rem;
 text-align: center;
  height: auto;
  flex: 0 0 60%;
 
}
.first-child img {
  width: 20px;
  height: 25px;
}

