@media (max-width: 841px){
  * {
    font-size: 7px;
}
.first-child h3 {
  font-size: 25px;
  word-spacing: 0px;   
}
.mini a {
  font-size: 15px;
  }

.second h2 {
  flex-wrap: nowrap;
}
#adjust {
  width: 20px;
  height: 23px;
  margin-left: 12px;
  margin-top: 3px;
  position: absolute;
}
.media {
  display: flex;
  flex-direction: column;
  
}
.image {
  width: 100%;
  max-width: 100%;

}
.image iframe {
  width: 100%;
  max-width: 100%;
}
.video {
  height: 400px;
   width : 100%;
   margin-top: 20px;
}
.up.show {
 
  right: 0px;
  opacity: 1;
  font-size: 20px;
}

}
